
.wrapper {
  min-height: 100%;
  margin: auto; 
  -webkit-align-content: center; 
          align-content: center; 
  padding-bottom: 100px;
  border: 3px solid black; 
}
nav.navbar.navbar-default {
  margin-top: 0;

}

nav {
  min-height: 100px;
  background: #E5E5E5;
  position: absolute;
}

nav.navbar {
  position: relative;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 3px;
  background: pink;
}

li.nav-link {
  -webkit-flex-direction: row;
          flex-direction: row; 
  -webkit-align-content: center; 
          align-content: center; 
}
div.home-header {
  /* /* height: 150; */

  font-family: 'Pacifico',
    cursive;
  background-color: pink;
  margin-bottom: 10pt; 
}



img.logo {
  max-width: 100%;
}
.measurement {
    /* border-radius: 50%; */
    background-color: white; 
    display: -webkit-flex; 
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    overflow: hidden;
    width: 150px;
    height: 100px;
}

.measurement img {
    -webkit-flex-shrink: 0;
            flex-shrink: 0;
    width: 200px;
    height: 100px;
}
/* .toggleButtons {
    justify-content: center;
} */

.card-container {
    padding: 10px 10px;
    width: 100%;
    justify-items: center;
}
.food-group {
    display: block;
    width: 500px;
    background: lightcoral;
    border-radius: 8px;
    color: white;
    font-size: 15px;
}

.card {
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
    transition: 0.3s;
    width: 40%;
    border-radius: 5px;
    width: 500px;

  }
  
  .card:hover {
    box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
  }

  
div.header{
    font-family: "Pacifico", cursive; 
    background-color: pink;
    margin-bottom: 0; 
    padding-bottom: 10pt; 
    padding-top: 10pt; 

}
